
.buttons{
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding: 20px;
    border-radius: 10px;
}

.buttons.theme-1{
    background-color: hsla(223, 31%, 20%,0.6);
}

.buttons.theme-2{
    background-color: hsla(0, 5%, 81%,0.6);

}
.buttons.theme-3{
    background-color: hsla(268, 71%, 12%,0.6);

}


.buttons div{
    width: 100%;
    background-color: transparent;
    display: flex;
    flex-wrap: nowrap;
    gap: 15px; 
    justify-content: space-around;
}

.container{
    width: 36%;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

@media screen and (max-width : 1400px) {
    .container{
        width: 60%;
    }   
}

@media screen and (max-width : 900px) {
    .container{
        max-width: 90%;
        width: auto;
        display: flex;
        gap: 20px;
        padding: 10px 0px 20px 0px;
    }   

    .buttons{
        flex-direction: row;
        justify-content: space-between;
        flex-wrap: wrap;
        gap: 14.5px;
    }
}

@media screen and (max-height : 700px) {
    .container{
        padding: 20px 0px 20px 0px;

    }   
}