*{
    margin: 0%;
    border: 0cm;
    list-style: none;
    text-decoration: none;
    padding: 0%;
    outline: none;
}

html, #root, body{
    width: 100%;
    height: 100%;
}

#root{
    display: flex;
    justify-content: center;
    align-items: center;
}

#root.theme-1{
     background-color: hsl(222, 26%, 31%);;
}

#root.theme-2{
    background-color: hsl(0, 0%, 93%);
}

#root.theme-3{
    background-color: hsl(268, 75%, 9%);
}

@media screen and (max-width : 900px) {
    html, #root, body{
        width: 100%;
        min-height: 100%;
        max-height: max-content;
        height: auto;
    }
    
}

@media screen and (max-height : 700px) {
    html, #root, body{
        width: 100%;
        min-height: 100%;
        max-height: max-content;
        height: auto;
    }
}